@import '../../style/variables.scss';

.app {
    &__banner {
        width: 100%;
        padding: 0 25px 0 45px;

        background-color: $dark-light;
        border-radius: 5px;

        display: flex;
        justify-content: space-between;
        align-items: center;

        &-text {
            text-align: center;
            font-weight: 700;
            font-size: 24px;
            line-height: 32px;
            color: #FFFFFF;
        }
    }
}